import { AUDIO_URLS, MASTER_VOLUME, MUSIC_VOLUME, SFX_VOLUME, type SfxKey } from '@/config/audio';

type Nullable<T> = T | null;

interface AudioEngine {
  unlock(): Promise<void>;
  playSfx(key: SfxKey): void;
  startBgm(): Promise<void>;
  stopBgm(): void;
  handleVisibility(hidden: boolean): void;
  setMuted(muted: boolean): void;
  isMuted(): boolean;
}

let singleton: Nullable<AudioEngine> = null;

export function getAudioEngine(): AudioEngine {
  if (singleton) return singleton;

  //   
  let ctx: Nullable<AudioContext> = null;
  let unlocked = false;

  const sfxBuffers: Partial<Record<SfxKey, AudioBuffer>> = {};
  const sfxLastPlayMs: Partial<Record<SfxKey, number>> = {};

  let bgmBuffer: Nullable<AudioBuffer> = null;
  let bgmSource: Nullable<AudioBufferSourceNode> = null;
  let bgmGain: Nullable<GainNode> = null;
  let bgmPlaying = false;
  let bgmShouldPlay = false;
  let gestureFallbackInstalled = false;
  let masterMuted = false;

  //   mute  persistent-
  try {
    const saved = localStorage.getItem('audio:muted');
    if (saved != null) {
      masterMuted = saved === '1' || saved === 'true';
    }
    } catch {
      //      ( ,  storage  ..)
    }

  function ensureCtx(): AudioContext {
    if (!ctx) {
      // @ts-expect-error: webkitAudioContext      (iOS Safari)
      const AC = window.AudioContext || window.webkitAudioContext;
      ctx = new AC();
    }
    return ctx;
  }

  async function resumeCtx(): Promise<void> {
    const c = ensureCtx();
    if (c.state === 'suspended') {
      try {
        await c.resume();
      } catch {
        //   resume:      suspended
      }
    }
  }

  function installGestureUnlockFallback(): void {
    if (gestureFallbackInstalled) return;
    gestureFallbackInstalled = true;

    const pointerOpts: AddEventListenerOptions = { passive: true, capture: true };
    const keydownCapture = true;

    const cleanup = () => {
      window.removeEventListener('pointerdown', tryResume, pointerOpts);
      window.removeEventListener('pointerup', tryResume, pointerOpts);
      window.removeEventListener('touchstart', tryResume, pointerOpts);
      window.removeEventListener('touchend', tryResume, pointerOpts);
      window.removeEventListener('keydown', onKeyDown, keydownCapture);
    };

    const afterRunning = () => {
      //   ,    
      try {
        const c = ensureCtx();
        const silentBuffer = c.createBuffer(1, 1, Math.max(22050, c.sampleRate));
        const src = c.createBufferSource();
        src.buffer = silentBuffer;
        const gain = c.createGain();
        gain.gain.value = 0.00001;
        src.connect(gain);
        gain.connect(c.destination);
        try {
          src.start(0);
        } catch {
          //     
        }
        try {
          src.stop(0);
        } catch {
          //     
        }
      } catch {
        //      ,   
      }

      //    ,  
      if (bgmShouldPlay && !bgmPlaying) {
        void startBgmInternal();
      }
      cleanup();
    };

    const tryResume = () => {
      try {
        const c = ensureCtx();
        //  :   running
        if (c.state === 'running') {
          afterRunning();
          return;
        }
        //   resume     
        void c
          .resume()
          .then(afterRunning)
          .catch(() => {
            //    resume:        
          });
      } catch {
        //      resume
      }
    };

    const onKeyDown = (): void => {
      tryResume();
    };

    window.addEventListener('pointerdown', tryResume, pointerOpts);
    window.addEventListener('pointerup', tryResume, pointerOpts);
    window.addEventListener('touchstart', tryResume, pointerOpts);
    window.addEventListener('touchend', tryResume, pointerOpts);
    window.addEventListener('keydown', onKeyDown, keydownCapture);
  }

  async function decodeUrl(url: string): Promise<AudioBuffer> {
    const c = ensureCtx();
    const res = await fetch(url);
    const buf = await res.arrayBuffer();
    return await new Promise<AudioBuffer>((resolve, reject) => {
      // decodeAudioData  Promise,      ,
      //         Web Audio
      void c.decodeAudioData(buf, resolve, reject);
    });
  }

  async function ensureSfxLoaded(key: SfxKey): Promise<void> {
    if (sfxBuffers[key]) return;
    const url = AUDIO_URLS.sfx[key];
    sfxBuffers[key] = await decodeUrl(url);
  }

  function playSfxNow(key: SfxKey): void {
    const c = ensureCtx();
    const buffer = sfxBuffers[key];
    if (!buffer) return;

    const source = c.createBufferSource();
    source.buffer = buffer;

    const gain = c.createGain();
    const vol = Math.max(0, Math.min(1, (SFX_VOLUME[key] ?? 1) * MASTER_VOLUME)) * (masterMuted ? 0 : 1);
    gain.gain.value = vol;

    source.connect(gain);
    gain.connect(c.destination);

    try {
      source.start(0);
    } catch {
      //     SFX
    }
  }

  async function loadBgmInternal(): Promise<void> {
    if (bgmBuffer) return;
    bgmBuffer = await decodeUrl(AUDIO_URLS.bgm);
  }

  function stopBgmInternal(): void {
    if (bgmSource) {
      try {
        bgmSource.stop(0);
      } catch {
        //    BGM  
      }
      try {
        bgmSource.disconnect();
      } catch {
        //   disconnect  
      }
      bgmSource = null;
    }
    bgmPlaying = false;
  }

  async function startBgmInternal(): Promise<void> {
    await resumeCtx();
    await loadBgmInternal();
    if (!bgmBuffer || bgmPlaying) return;

    const c = ensureCtx();
    const src = c.createBufferSource();
    src.buffer = bgmBuffer;
    src.loop = true;

    const gain = c.createGain();
    const base = Math.max(0, Math.min(1, MUSIC_VOLUME * MASTER_VOLUME));
    gain.gain.value = masterMuted ? 0 : base;

    src.connect(gain);
    gain.connect(c.destination);

    try {
      src.start(0);
    } catch {
      //    BGM:      
    }

    bgmSource = src;
    bgmGain = gain;
    bgmPlaying = true;
  }

  singleton = {
    async unlock(): Promise<void> {
      await resumeCtx();
      unlocked = true;

      //  iOS/Safari    start()    
      //     ,    
      try {
        const c = ensureCtx();
        const silentBuffer = c.createBuffer(1, 1, Math.max(22050, c.sampleRate));
        const src = c.createBufferSource();
        src.buffer = silentBuffer;
        const gain = c.createGain();
        gain.gain.value = 0.00001;
        src.connect(gain);
        gain.connect(c.destination);
        try {
          src.start(0);
        } catch {
          //     
        }
        try {
          src.stop(0);
        } catch {
          //     
        }
      } catch {
        //      ,   
      }

      //   SFX ( await   )
      void ensureSfxLoaded('jump');
      void ensureSfxLoaded('blight');
      void ensureSfxLoaded('skeleton');
      void ensureSfxLoaded('coin');
      void ensureSfxLoaded('heart');
      // BGM     

      //     ,     unlock
      if (bgmShouldPlay && !bgmPlaying) {
        try {
          await startBgmInternal();
        } catch {
          //    BGM  
        }
      }
    },

    playSfx(key: SfxKey): void {
      //    (unlock)   
      if (!unlocked) return;
      //   ,      
      const now = performance.now();
      const minGapMs = key === 'coin' || key === 'heart' ? 40 : 20;
      const last = sfxLastPlayMs[key] ?? 0;
      if (now - last < minGapMs) return;
      sfxLastPlayMs[key] = now;

      if (!sfxBuffers[key]) {
        //          
        void ensureSfxLoaded(key).then(() => playSfxNow(key)).catch(() => {});
      } else {
        playSfxNow(key);
      }
    },

    async startBgm(): Promise<void> {
      bgmShouldPlay = true;
      if (!unlocked) return;
      try {
        await startBgmInternal();
      } catch {
        //     
      }
    },

    stopBgm(): void {
      stopBgmInternal();
      bgmShouldPlay = false;
    },

    handleVisibility(hidden: boolean): void {
      //  ,    
      if (!unlocked) return;
      if (hidden) {
        stopBgmInternal();
      } else {
        if (bgmShouldPlay) { void startBgmInternal(); }
      }
    },

    setMuted(muted: boolean): void {
      masterMuted = !!muted;
      //   mute
      try {
        localStorage.setItem('audio:muted', masterMuted ? '1' : '0');
      } catch {
        //     
      }
      //   gain  ,    
      try {
        const base = Math.max(0, Math.min(1, MUSIC_VOLUME * MASTER_VOLUME));
        if (bgmGain) {
          bgmGain.gain.value = masterMuted ? 0 : base;
        }
      } catch {
        //    
      }
    },

    isMuted(): boolean {
      return !!masterMuted;
    },
  };

  //   fallback     
  //        resume
  // ( ,     )
  installGestureUnlockFallback();

  return singleton;
}
