<template>
  <div class="gui">
    <div class="gui__score">
      <div class="gui-icon gui-icon--score"></div>
      <span></span>
      <span class="score-value">{{ coinCount }}</span>
    </div>

    <div class="gui__life">
      <div class="gui-life">
        <div
          v-for="i in Math.max(0, Math.min(3, lifeCount))"
