        <div class="gui-icon gui-icon--sound"></div>
      </div>
    </div>

    <div class="gui__pause">
      <div
        class="gui-button"
        :class="{ paused: isPaused }"
        @click="$emit('toggle-pause')"
        role="button"
        aria-label="Toggle pause"
        :aria-pressed="isPaused"
      >
        <div class="gui-icon gui-icon--pause"></div>
      </div>
    </div>

    <div class="gui__overlay"></div>

    <div class="gui__triggers">
      <button
        v-if="currentButton === 'start'"
        class="gui-button gui-button--start"
        @click="$emit('start')"
        key="start"
      >
        <div class="gui-icon gui-icon--start"></div>
      </button>

      <button
        v-else-if="currentButton === 'restart'"
        class="gui-button gui-button--restart"
        @click="$emit('restart')"
        key="restart"
      >
        <div class="gui-icon gui-icon--restart"></div>
      </button>
    </div>
  </div>
</template>

<script setup lang="ts">
defineProps<{
  coinCount: number;
  lifeCount: number;
  isMuted: boolean;
  isPaused: boolean;
  currentButton: 'start' | 'restart' | null;
}>();

defineEmits<{
  (e: 'toggle-mute'): void;
  (e: 'toggle-pause'): void;
  (e: 'start'): void;
  (e: 'restart'): void;
}>();
</script>

